<script>
export default {
  name: 'MyButton',
  props: {
    // size:æé®çå¤§å°
    size: {
      type: String,
      default: 'medium',
      require: false,
      // æ ¡éªsizeçå¼ä¸º
      validator: function (val) {
        // arr.includes(val) å¤æ­æ°ç»ä¸­æ¯å¦å­å¨ï¼è¿åå¸å°å¼ï¼ä¸ºçä¸æ¥éï¼ä¸ºfalseæ¥é
        return ['mini', 'samll', 'tiny', 'medium', 'large', 'big', 'huge', 'massive'].includes(val)
      }
    },
    // å¾æ 
    icon: {
      type: String,
